import React from "react"
import Link from "gatsby-link"
import YellowButtonMD from "../components/Buttons/YellowButtonMD"

export default ({ data }) => {
    const logNode = data.markdownRemark;
    const log = logNode.frontmatter;
    
    console.log(data);

    return (
        <div>
            <div className="container">
                <div className="row">
                    <div className="col-lg-12">
                        <h1>
                            {log.title} &ndash; ({log.desc})
                        </h1>
                        <h5><span style={{color: 'rgba(218, 165, 32, 0.822)'}} className="glyphicon glyphicon-calendar"></span> {log.date}</h5>
                        <h5>Posted By: {log.author}</h5>
                        <hr />
                    </div>
                    <div className="col-lg-12">
                        <div dangerouslySetInnerHTML={{__html: logNode.html}} />
                    </div>
                    <div className="col-lg-12">
                        <YellowButtonMD path="/logs">&laquo; Return to Logs</YellowButtonMD>
                    </div>
                </div>
            </div>
        </div>
    )
}

export const query = graphql`
    query LogPostQuery($path: String!) {
        markdownRemark(frontmatter: {path: {eq: $path}}) {
            html
            frontmatter {
                title
                date(formatString: "MMMM DD, YYYY")
                author
                desc
            }
        }
    }
`


